.upper-wrapper {
  display: flex;
}

.top-field,
.bottom-field,
.left-side,
.right-side {
  position: absolute;
}

.left-side,
.right-side {
  min-width: 200px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.left-side {
  bottom: 0;
  left: 0;
}

.right-side {
  top: 0;
  right: 0;
}

.middle-part {
  display: block;
  flex-grow: 1;
  margin: 0 2px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  position: relative;
}
.entity-field {
  position: relative;
  min-width: 800px;
  min-height: 800px;
}
.top-field,
.bottom-field {
  min-height: 200px;
  display: flex;
  justify-content: flex-start;
}

.top-field {
  top: 0;
  left: 0;
}

.bottom-field {
  bottom: 0;
  right: 0;
}
